import React from "react";

function Person(props) {
  return (
    <div>
      <img
        class="noSelect noDrag"
        src={props._counter.sprite}
        alt="highlighted person"
        style={getPersonPositionStyle(props.id, props.cLength, props.r)}
        onClick={() => {
          props._onClick(props.id);
        }}
      />
      <div
        class="noSelect goalText"
        style={getTextPositionStyle(props.id, props.cLength, 1.5 * props.r)}
      >
        {props._counter.value} / {props.cLength - 1}
      </div>
    </div>
  );
}

function getPersonPositionStyle(id, cLength, r) {
  const theta = getTheta(id, cLength);
  let point = getPoint(theta, r);
  return {
    position: "absolute",
    overflow: "auto",
    top: point[0],
    left: point[1],
    width: "40px",
    height: "auto",
    transform: "translate(-50%, -50%)",
  };
}

function getTextPositionStyle(id, cLength, r) {
  const theta = getTheta(id, cLength);
  let point = getPoint(theta, r);
  return {
    position: "absolute",
    overflow: "auto",
    top: point[0],
    left: point[1],
    whiteSpace: "nowrap",
    transform: "translate(-50%, -50%)",
  };
}

function getTheta(id, cLength) {
  return (2 * Math.PI * (id - 1)) / cLength;
}

function getPoint(theta, r) {
  return [Math.cos(theta) * r, Math.sin(theta) * r];
}

export default Person;
