import React from "react";
import imgPixel from "../images/Pixel.png";

function Line(props) {
  return (
    <img
      class="noSelect noDrag nearInterp line"
      src={imgPixel}
      alt="line"
      style={getLinePositionStyle(props.points, props.r, props.cLength)}
    />
  );
}

function getLinePositionStyle(points, r, cLength) {
  const theta0 = getTheta(points.p1, cLength);
  const theta1 = getTheta(points.p2, cLength);
  let p0 = getPoint(theta0, r);
  let p1 = getPoint(theta1, r);
  return {
    position: "absolute",
    overflow: "auto",
    top: (p0[0] + p1[0]) / 2.0,
    left: (p0[1] + p1[1]) / 2.0,
    transform: getStyleTransform(p0, p1),
    zIndex: -1,
    imageRendering: "pixelated",
  };
}

function getTheta(id, cLength) {
  return (2 * Math.PI * (id - 1)) / cLength;
}

function getPoint(theta, r) {
  return [Math.cos(theta) * r, Math.sin(theta) * r];
}

function getAngle(p0, p1) {
  p1 = [p1[0] - p0[0], p1[1] - p0[1]];
  p0 = [1, 0];
  let angle = Math.atan2(p1[0], p1[1]) / (2 * Math.PI);
  return angle;
}

function getDist(p0, p1) {
  let p = [p0[0] - p1[0], p0[1] - p1[1]];
  return Math.pow(p[0] * p[0] + p[1] * p[1], 0.5);
}

function getStyleTransform(p0, p1) {
  return (
    "rotate(" + getAngle(p0, p1) + "turn) scale(" + getDist(p0, p1) + ", 10)"
  );
}

export default Line;
