import React, {useReducer} from 'react';
import './App.css';
import Person from './components/person';
import Line from './components/line';
import NavBar from './components/navbar';
import imgPerson from "./images/Person.png";
import imgBorderPerson from "./images/BorderPerson.png";
import imgBorderPersonBlue from "./images/BorderPerson_New.png";

function App() {
  const [state, dispatch] = useReducer((state, action) => {
    switch(action.kind) {
      case 'SETVAL':
        return {
        ...state,
        counters: setCounterVal(state.counters, action.id, action.val),
      }

      case 'ADD':
        return {
        ...state,
        lines: [],
        counters: setCounterNum(state.counters.length+1),
      }

      case 'REMOVE':
        return {
        ...state,
        lines: [],
        counters: setCounterNum(state.counters.length-1),
      }

      case 'SELECT':
        return {
        ...state,
        counters: setCounterSprites(state.counters, action.val),
        selected: action.val,
      }

      case 'ADDLINE':
        let newLines = drawLine(state.lines, action.p1, action.p2);
        return {
          ...state,
          lines: newLines,
          counters: setCounterVals(state.counters, newLines),
      }

      case 'RESET':
        return {
          ...state,
          counters: resetCounters(state.counters),
          lines: [],
      }

      default: return state;
    }
  }, {
    counters: [
      { value: 0, sprite: imgPerson },
      { value: 0, sprite: imgPerson },
      { value: 0, sprite: imgPerson },
    ],
    lines: [],
    selected: null,
  });

  const select = (a) => { 
    dispatch({kind:'SELECT', val:a});
  }

  const personOnClick = (num) => {
    if(state.selected == null) {
      select(num);
      return;
    }
    else if(state.selected !== num) {
      dispatch({kind:"ADDLINE", p1:state.selected, p2:num});
    }
    
    select(null);
  }

  return (
    <div>
      <NavBar totalCounters = {0} />
      <button class="btn noSelect noDrag" onClick={() => {dispatch({kind:"RESET"})}}>Reset</button>
      <button class="btn btn-circle noSelect noDrag" onClick={() => {dispatch({kind:"REMOVE"})}}>-</button>
      <button class="btn btn-circle noSelect noDrag" onClick={() => {dispatch({kind:"ADD"})}}>+</button>
      <main className="container">
        <div className="canvas">
          {state.counters.map((counter, i) =>
              Person({id:i, _counter:counter, _onClick:personOnClick, cLength:state.counters.length, r:150.0})
          )}
          {state.lines.map((line) =>
            Line({points:line, r:150.0, cLength:state.counters.length})
          )}
        </div>
      </main>
    </div>
  );
}

function setCounterVals(counters, lines) {
  let newCounters = counters.map((counter) => {return {value:0, sprite:counter.sprite}});
  for(let i = 0; i < newCounters.length; i++) {
    for(let j = 0; j < lines.length; j++) if(lines[j].p1 === i || lines[j].p2 === i) newCounters[i].value++;
    newCounters[i].sprite = (newCounters[i].value === counters.length-1) ? imgBorderPersonBlue : newCounters[i].sprite;
  }
  return newCounters;
}

function drawLine(lines, pIndex1, pIndex2) {
  let _p1 = pIndex1 < pIndex2 ? pIndex1 : pIndex2;
  let _p2 = pIndex1 < pIndex2 ? pIndex2 : pIndex1;
  let test = false;
  for(let i = 0; i < lines.length; i++) {
    if(lines[i].p1 === _p1 && lines[i].p2 === _p2) test = true;
  }
  let newLines;
  if(test) newLines = lines.filter((line) => (line.p1 !== _p1 || line.p2 !== _p2));
  else {
    newLines = [...lines];
    newLines[newLines.length] = {p1:_p1, p2:_p2};
  }

  return newLines;
}

function setCounterSprites(array, selected) {
  let newArray = [...array];
  for(let i = 0; i < newArray.length; i++) {
    if(i === selected) newArray[i].sprite = imgBorderPerson;
    else newArray[i].sprite = (newArray[i].value === newArray.length-1) ? imgBorderPersonBlue : imgPerson;
  }
  return newArray;
}

function setCounterVal(array, id, val) {
  let newArray = [...array];
  newArray[id].value = val;
  return newArray;
}

function setCounterNum(num) {
  let newArray = [{ value: 0, sprite: imgPerson },];
  for(let i = 1; i < ((num < 11) ? num : 11); i++) newArray[i] = { value: 0, sprite: imgPerson };
  return newArray;
}

function resetCounters(array) {
  let newArray = [...array]
  for(let i = 0; i < newArray.length; i++) newArray[i] = { value: 0, sprite: imgPerson };
  return newArray;
}

export default App;
